import React, {useState, useEffect} from 'react';
import './BoardPage.css';
import axios from 'axios';
import {Link} from 'react-router-dom';
import {Thread} from '../../commons/Thread';
import {NewThreadForm} from "../../commons/NewThreadForm";
import {Header} from "../../commons/Header";

export const BoardPage = ({ match }) => {
    const [boards, setBoards] = useState([]);
    const [threadInfo, setThreadInfo] = useState({posts: []});
    const id = match.params.id;
    const [isCreate, setCreate] = useState(false);

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/board/${id}/`
        }).then((res) => {
            let data = res.data
            data.posts.sort((a, b) => {
                return b.id - a.id
            })
            data.posts.map((el) => {
                el.comments.sort((a, b) => {
                    return b.id - a.id
                })
            })
            setThreadInfo(data);
        })
    }, [id])

    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/board/"
        }).then((res) => {
            setBoards(res.data);
        })
    }, [])

    function handleClick() {
        setCreate(!isCreate);
    }

    return (
        <div className="board">
            <Header />
            <div className="board__header">
                <h1 className="header__boardname">{threadInfo ? threadInfo.name : 'Loading'}</h1>
                <div className="board__create">
                    <span onClick={handleClick}>{isCreate ? "Close form" : "Create thread"}</span>
                    {isCreate && <NewThreadForm boardName={threadInfo.name} closeHandle={handleClick} />}
                </div>
            </div>
            <div className="board__wrapper">
                <aside className="board__navigation">
                    {boards.map((elem) => {
                        return <Link to={{pathname: `/board/${elem.id}`, fromDashboard: false}} key={elem.id}
                                     className='board__threadLink'>{elem.name}</Link>
                    })}
                </aside>
                <div className="board__boardsList">
                    {threadInfo && threadInfo.posts.map((elem) => {
                            const date = new Date(elem.pub_date);
                            return <Thread key={elem.id} id={elem.id} content={elem.content} title={elem.title}
                                           date={date.toLocaleString()}
                                           number={elem.slug.slice(elem.slug.indexOf('-') + 1)}
                                           comments={elem.comments.slice(0, 3)} allComments={elem.comments}/>
                        }
                    )}
                </div>
            </div>
        </div>
    )
}