import React, { useEffect, useState } from 'react';
import './ThreadDetailPage.css'
import { Thread } from "../../commons/Thread";
import axios from "axios";
import {Header} from "../../commons/Header";


export const ThreadDetailPage = ({ match }) => {
    const [data, setData] = useState({comments: []});
    const id = match.params.id;

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8000/api/thread/${id}/`
        }).then((res) => {
            let data = res.data
            data.comments.sort((a, b) => {
                return b.id - a.id
            })
            setData(data);
        })
    }, [id])

    return (
        <>
            <Header />
            {data && <div className="thread__page">
                    <Thread content={data.content} title={data.title} id={id}
                                               date={new Date(data.pub_date).toLocaleString()}
                                               number={data.slug}
                                               comments={data.comments}/>
                </div>}
        </>
    )

}
